.theme-enter{
   opacity:0;
   transform: scale(0.0);
}

.theme-enter-active{
   opacity:1;
   transform: scale(1.0);
   transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.theme-exit-active{
   opacity:0;
   transform: scale(0.0);
   transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1);
}